import React from "react";
import _ from "lodash";
import config from "../config";

const routers = _.get(config, "routers");

function HomePage() {
  return (
    <div>
      Welcome to Next.js!
      <h1>Menu</h1>
      {_renderRouters(routers)}
    </div>
  );
}

const _renderRouters = routers => {
  if (!_.isEmpty(routers)) {
    return _.map(routers, (group, idxGroup) => {
      return (
        <div>
          <h3>{_.get(group, "text")}</h3>
          <ul key={idxGroup}>
            <li>
              {!_.isEmpty(group.subRouters) &&
                _.map(group.subRouters, (router, index) => {})}
            </li>
          </ul>
        </div>
      );
    });
  }
};

export default HomePage;
